@import 'leaflet/dist/leaflet.css';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'SF Pro Display', 'SF Pro Text', 'Helvetica Neue', 'Arial', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F5F1E0;
  /* Fix content overlap with iOS status bar/notch */
  padding-top: env(safe-area-inset-top);
  /* Fix content overlap with home indicator */
  padding-bottom: env(safe-area-inset-bottom);
  /* Ensure the app fills the entire screen, including the safe areas */
  min-height: 100vh;
  min-height: 100dvh; /* Dynamic viewport height for better mobile support */
  width: 100vw;
  /* Prevent zoom on double tap and text selection */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  /* Prevent bounce scrolling on iOS */
  overscroll-behavior: none;
  /* Smooth scrolling */
  scroll-behavior: smooth;
  /* Prevent text size adjustment */
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  /* Prevent viewport changes after interactions */
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* Prevent iOS keyboard from resizing viewport */
  -webkit-overflow-scrolling: touch;
  overflow: hidden;
}

#root {
  /* Ensure root container fills the fixed body */
  height: 100vh;
  height: 100dvh; /* Dynamic viewport height for better mobile support */
  width: 100%;
  /* Enable scrolling within the app */
  overflow-y: auto;
  overflow-x: hidden;
  /* Ensure proper stacking context */
  position: relative;
  /* Native app feel */
  -webkit-overflow-scrolling: touch;
  /* Prevent viewport changes */
  contain: layout style paint;
}

code {
  font-family: 'SF Mono', Monaco, 'Cascadia Code', 'Roboto Mono', Consolas, 'Courier New', monospace;
}

/* Native app touch interactions */
* {
  /* Improve touch responsiveness */
  -webkit-tap-highlight-color: transparent;
  /* Prevent text selection on UI elements */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Allow text selection in input fields and text content */
input, textarea, [contenteditable] {
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}

/* Native button styling */
button {
  /* Remove default button styling */
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  font: inherit;
  color: inherit;
  cursor: pointer;
  /* Native touch feedback */
  -webkit-tap-highlight-color: transparent;
  /* Smooth transitions */
  transition: all 0.2s ease;
}

/* Native scrollbars for webkit browsers */
::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

/* Prevent pull-to-refresh on mobile */
body {
  overscroll-behavior-y: contain;
}

/* iOS-specific fixes for viewport stability */
@media screen and (max-width: 768px) {
  /* Prevent viewport changes when virtual keyboard appears */
  body {
    /* Use fixed positioning to prevent viewport resize */
    position: fixed !important;
    width: 100vw !important;
    height: 100vh !important;
    height: 100dvh !important;
  }
  
  /* Ensure alerts don't cause viewport changes */
  .in-app-alert {
    /* Prevent alerts from affecting viewport */
    position: fixed !important;
    z-index: 999999 !important;
    /* Use safe area insets for positioning */
    top: max(20px, env(safe-area-inset-top) + 10px) !important;
    right: max(20px, env(safe-area-inset-right) + 10px) !important;
    left: max(20px, env(safe-area-inset-left) + 10px) !important;
  }
  
  /* Prevent input focus from changing viewport */
  input, textarea, select {
    /* Prevent zoom on focus */
    font-size: 16px !important;
    /* Prevent viewport changes */
    transform: translateZ(0);
    -webkit-transform: translateZ(0);
  }
  
  /* Prevent button clicks from affecting viewport */
  button, .clickable, [role="button"] {
    /* Prevent viewport changes on tap */
    transform: translateZ(0);
    -webkit-transform: translateZ(0);
    /* Ensure touch events don't affect viewport */
    touch-action: manipulation;
  }
  
  /* Prevent modal backdrops from affecting viewport */
  .chatbot-modal-backdrop, .modal-backdrop {
    /* Fixed positioning to prevent viewport changes */
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    right: 0 !important;
    bottom: 0 !important;
    width: 100vw !important;
    height: 100vh !important;
    height: 100dvh !important;
  }
}
